@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
}
.App {
  font-family: "Roboto", sans-serif;
  text-align: center;
  background-color: #005b96;
  overflow-x: hidden;
}

.card-img {
  width: 200px;
}
.card {
  border-radius: 10px;
}
.card:hover {
  background-color: #e2e2e2;
}

.coin-input {
  border-radius: 15px;
  padding: 0 15px;
}
.coin-input:focus {
  outline: none;
}
.crypto {
  min-height: 100vh;
}
.btn {
  margin: 10px 0px;
  border-radius: 20px;
}

